.cat-fact {
    background-color: #F6D8AE;
    border: 2px solid #F9C794;
    border-radius: 8px;
    padding: 16px;
    margin: 16px;
    text-align: center;
  }
  
  .cat-fact h2 {
    font-family: 'Catamaran', sans-serif;
    font-size: 28px;
    margin-bottom: 8px;
  }
  
  .cat-fact p {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    margin-bottom: 16px;
  }
  
  .cat-fact button {
    background-color: #F9C794;
    border: none;
    border-radius: 4px;
    color: white;
    font-family: 'Catamaran', sans-serif;
    font-size: 16px;
    padding: 8px 16px;
    transition: background-color 0.3s;
  }
  
  .cat-fact button:hover {
    background-color: #F6D8AE;
    cursor: pointer;
  }
  