.card {
    background-color: #f0f0f0;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s ease-in-out;
  }
  
  .card:hover {
    transform: scale(1.05);
  }
  
  .image-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 100%;
    height: auto;
    border-radius: 50%;
  }
  
  .details {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .name {
    color: #333;
    font-size: 24px;
    font-weight: bold;
    margin: 10px 0;
  }
  
  .email {
    color: #666;
    font-size: 18px;
    margin: 10px 0;
  }
  
  @media screen and (max-width: 600px) {
    .card {
      padding: 10px;
      max-width: 400px;
    }
  
    .name {
      font-size: 20px;
    }
  
    .email {
      font-size: 16px;
    }
  }
  